<template>
  <main>
    <div class="inner">
      <ul id="menu">
        <li
          v-for="(row, idx) in categories"
          :key="idx"
          :class="{ current: menuIndex === idx }"
          @mouseenter="handleMouseEnter(idx)"
          @mouseleave="handleMouseLeave(idx)"
        >
          <a
            href="'#menuCon' + (idx + 1)"
            :class="iconImg[idx]"
            :rel="'menuCon' + (idx + 1)"
            @click.prevent="
              menuIndex = idx;
              loadData(row, 1);
            "
            >{{ row }}</a
          >
          <ul
            class="dash"
            v-show-slide:300:ease-in-out="active1[idx] || menuIndex === idx"
          >
            <li :class="{ on: selectMenuIndex === `${row}1` }">
              <a
                href="#"
                @click.prevent="
                  menuIndex = idx;
                  loadData(row, 1);
                "
                >고객</a
              >
            </li>
            <li :class="{ on: selectMenuIndex === `${row}2` }">
              <a
                href="#"
                @click.prevent="
                  menuIndex = idx;
                  loadData(row, 2);
                "
                >가맹점</a
              >
            </li>
          </ul>
        </li>
      </ul>
      <div id="content">
        <div class="faqWrap" id="menuCon">
          <h2 class="title">
            <span>{{ this.subtitle }}</span>
            <span>{{ this.title }}</span>
          </h2>
          <ul class="tabBtn">
            <li
              v-for="(row, idx) in subMenu"
              :key="idx"
              :class="{ on: idx === subMenuIndex }"
            >
              <h3>
                <button type="button" @click="getSubMenuData(row.srl, idx)">
                  {{ row.title }}
                </button>
              </h3>
            </li>
          </ul>
          <div class="faqListWrap">
            <div id="faqList" class="con">
              <dl v-for="(row, idx) in dataList" :key="row.title">
                <transition name="fade" mode="out-in">
                  <dt
                    v-if="active"
                    class="tit"
                    :class="{ on: selectIndex === idx }"
                    @click="getContents(idx)"
                  >
                    <span class="type">{{ row.title }}</span>
                  </dt>
                </transition>
                <dd
                  class="txt"
                  v-show-slide:500:ease="selectIndex === idx"
                  v-html="row.content"
                ></dd>
              </dl>
            </div>
          </div>
        </div>
        <!-- <div class="listMore taC">
          <button
            type="button"
            @click="getDataList(this.srl, this.recordsPerPage)"
          >
            10개 더보기
          </button>
          <em class="totalList">총 {{ this.totalRecords }}개</em>
        </div> -->
      </div>
    </div>
  </main>

  <div class="mask"></div>
  <button type="button" id="top"><span>top</span></button>
  <div class="mobileMenuWrap">
    <div class="mobileLogo">
      <img :src="require('@/assets/images/logo.png')" alt="대구로" />
      <em>고객지원</em>
      <button type="button" class="close">모바일 메뉴 닫기</button>
    </div>
    <ul class="mobileMenu">
      <li v-for="(row, idx) in categories" :key="idx">
        <em :class="'tit ' + iconImg[idx]">{{ row }}</em>
        <ul class="dash dot">
          <li :class="{ on: selectMenuIndex === `${row}1` }">
            <a
              href="#"
              @click.prevent="
                menuIndex = idx;
                loadData(row, 1);
                removeMobileMenu();
              "
              >고객</a
            >
          </li>
          <li :class="{ on: selectMenuIndex === `${row}2` }">
            <a
              href="#"
              @click.prevent="
                menuIndex = idx;
                loadData(row, 2);
                removeMobileMenu();
              "
              >가맹점</a
            >
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "HelloWorld",

  data: () => ({
    iconImg: [
      "menuIco delivery",
      "menuIco taxi",
      "menuIco reservation",
      "menuIco flower",
      "menuIco bus",
    ],
    categories: [],
    dataList: [],
    subMenu: [],
    menuIndex: 0,
    selectMenuIndex: "카카오 T1",
    subMenuIndex: 0,
    menuCon: "카카오 T",
    title: "",
    subtitle: "",
    srl: 0,
    recordsPerPage: 10,
    totalRecords: 0,
    selectIndex: null,
    active: true,
    active1: {},
  }),
  mounted() {
    this.loadData(this.menuCon, 1);
    function mobileRemove() {
      $(".mobileMenuWrap").removeClass("on");
      $(".mask").removeClass("on");
    }
    $(function () {
      $(".btnSearch").click(function () {
        $(this).toggleClass("on");
        $(".search").slideToggle();
      });

      $(".btnMenu").click(function () {
        $(this).toggleClass("on");
        $(".mobileMenuWrap").addClass("on");
        $(".mask").toggleClass("on");
      });

      $(".mobileMenuWrap .close").click(function () {
        mobileRemove();
      });

      $(window).on("load resize", function () {
        var w = $(window).width();
        var x = 1024;
        if (w > x) {
          mobileRemove();
        }
      });

      // top
      $("#top").click(function () {
        $("html, body").stop().animate(
          {
            scrollTop: 0,
          },
          500
        );
      });

      $(window).scroll(function () {
        if ($(this).scrollTop() > 100) {
          $("#top").addClass("on");
        } else {
          $("#top").removeClass("on");
        }
      });
    });
  },
  methods: {
    async loadData(menuCon, div) {
      this.subMenuIndex = 0;
      this.selectIndex = null;
      this.selectMenuIndex = menuCon + div;
      this.getCategories(menuCon, div).then(() => {
        this.getDataList(this.srl, 10);
      });
    },
    removeMobileMenu() {
      $(".mobileMenuWrap").removeClass("on");
      $(".mask").removeClass("on");
    },
    async getCategories(menuCon, div) {
      await this.$axios
        .get("https://service.kakaomobility.com/api/cs/v1/faqs/categories/")
        .then((response) => {
          let newCategories = [];
          let newSubMenu = [];
          let newSubMenu1 = [];
          let uniqueParentSrl = null;
          response.data.forEach((item) => {
            if (item.title != "고객" && item.title != "파트너") {
              newCategories.push(item.title);
            }
            if (item.parent_name === menuCon) {
              if (uniqueParentSrl === null) {
                uniqueParentSrl = item.parent_srl;
              }
              if (item.parent_srl === uniqueParentSrl) {
                newSubMenu.push(item);
              } else {
                newSubMenu1.push(item);
              }
            }
          });
          console.log(newSubMenu);
          console.log(newSubMenu1);
          this.srl = div === 1 ? newSubMenu[0].srl : newSubMenu1[0].srl;
          this.title = menuCon;
          this.subtitle = div === 1 ? "고객" : "가맹점";
          this.categories = newCategories.slice(0, 5);
          this.subMenu = div === 1 ? newSubMenu : newSubMenu1;
        });
    },
    getDataList(srl, recordsPerPage) {
      this.$axios
        .get(
          `https://service.kakaomobility.com/api/cs/v1/faqs/categories/${srl}/contents/?srl=${srl}&recordsPerPage=100&currentPageNo=1`
        )
        .then((response) => {
          this.totalRecords = response.data.total_records;
          this.dataList = response.data.contents.map((item) => {
            return { ...item, uniqueKey: `key-${item.title}` };
          });
        });
      this.recordsPerPage = recordsPerPage + 10;
    },
    getSubMenuData(srl, idx) {
      if (this.subMenuIndex === idx) {
        return;
      }
      this.srl = srl;
      this.selectIndex = null;
      this.getDataList(srl, 10);
      this.active = false;
      setTimeout(() => {
        this.subMenuIndex = idx;
        this.$nextTick(() => {
          this.active = true;
        });
      }, 100);
    },
    getContents(idx) {
      this.selectIndex = this.selectIndex === idx ? null : idx;
    },
    handleMouseEnter(idx) {
      this.active1[idx] = true;
    },
    handleMouseLeave(idx) {
      this.active1[idx] = false;
    },
  },
};
</script>
<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
